.nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  background-color: #81b3d2;
}

.site-list__item {
  padding: 15px 0;
}

.site-list__link {
  color: #ffffff;
  text-decoration: none;
  padding: 5px 0;
}

@media (min-width: 768px) {
  .nav__list {
    background-color: #ffffff;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .nav__list::after {
    content: "";
    grid-column: 3/4;
    grid-row: 1/3;
  }

  .site-list__link {
    color: #000000;
  }
}
@media (min-width: 768px) {
  .header__logo-image {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.header {
  position: relative;
}

.nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
}

@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.main__preview {
  background-image: url("../img/logo/background-index.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 389px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main__preview-logo {
  background-image: url("../img/logo/background-preview.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 214px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main__block {
  padding: 35px 10px;
}

.block__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 20px 0;
  margin: 0;
}

.block__title--big {
  font-size: 21px;
}

.block__text {
  font-size: 14px;
  line-height: 21px;
  padding: 20px 0;
  margin: 0;
}

.block__find {
  text-decoration: none;
  padding: 15px 50px;
  background-color: #81b3d2;
  color: #ffffff;
}

@media (min-width: 768px) {
  .block__title {
    font-size: 21px;
  }

  .block__title--big {
    font-size: 30px;
  }

  .main__block {
    padding: 56px 125px;
  }
}
.advantages__article {
  margin: 0;
  padding: 35px 20px;
}
.advantages__article--bg-blue {
  background-color: #81b3d2;
  color: #ffffff;
}
.advantages__article--bg-grey {
  background-color: #eeeeee;
}

.advantages__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 20px 0;
}

.advantages__text {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 20px 0;
}

.advantages__benefits-number {
  margin: 0;
  padding: 20px 0;
}

@media (min-width: 768px) {
  .advantages__title {
    font-size: 21px;
  }
}
.services__list {
  margin: 0;
  padding: 35px 20px;
}

.list__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 20px 0;
}

.list__text {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 20px 0;
}

@media (min-width: 768px) {
  .list__title {
    font-size: 21px;
  }
}
@media (min-width: 768px) {
  .advantages__img-wrapper {
    width: 512px;
    height: 256px;
    flex-shrink: 0;
  }
}
@media (min-width: 768px) {
  .causes__advantages {
    display: flex;
    flex-wrap: wrap;
  }

  .advantages__article {
    box-sizing: border-box;
    min-height: 256px;
  }

  .advantages__services {
    display: flex;
  }

  .advantages__block {
    display: flex;
  }
  .advantages__block--reverse {
    flex-direction: row-reverse;
  }
  .advantages__block--triple {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.picture__sign {
  text-align: left;
  padding-left: 20px;
}

.picture__description {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.picture__name {
  font-size: 14px;
  line-height: 21px;
  text-transform: none;
}

.picture__like {
  text-align: left;
  padding-left: 20px;
}

.like__count {
  font-weight: 700;
}

@media (min-width: 768px) {
  .picture__block {
    display: flex;
    justify-content: space-between;
    background-color: #eeeeee;
  }

  .picture__block--grey {
    background-color: #eaeaea;
  }

  .picture__sign {
    padding-left: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .picture__like {
    padding-left: auto;
    padding-right: 20px;
    padding-top: 30px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1200px) {
  .photo__picture {
    position: relative;
  }

  .picture__block {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.2);
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .picture__scenery {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
  }

  .picture__scenery--big {
    height: 400px;
    width: 648px;
  }

  .picture__scenery--small {
    height: 267px;
    width: 324px;
  }

  .photo__block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .block__preview {
    height: 500px;
    width: 648px;
  }
}
@media (min-width: 1200px) {
  .picture__scenery--big {
    height: 400px;
    width: 1200px;
  }

  .picture__scenery--small {
    height: 267px;
    width: 400px;
  }

  .photo__block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.fieldset {
  margin: 0;
  border: none;
}

.fieldset__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 20px;
}

.form__text,
.form__info,
.form__radio,
.form__checkbox,
.form__textarea,
.form__button {
  padding: 0 40px;
  margin-bottom: 60px;
}

.text__label {
  font-size: 14px;
  line-height: 21px;
  text-align: start;
}

.text__section {
  margin-bottom: 15px;
}

.text__input {
  font-family: "PT Sans", sans-serif;
  height: 58px;
  border: none;
  padding: 16px;
  background-color: #f2f2f2;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.info__section {
  margin-bottom: 15px;
}

.info__input {
  font-family: "PT Sans", sans-serif;
  height: 58px;
  border: none;
  padding: 16px;
  background-color: #eeeeee;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.info__label {
  font-size: 14px;
  line-height: 21px;
  text-align: start;
}

.radio__text {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.checkbox__text {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.textarea__textarea {
  font-family: "PT Sans", sans-serif;
  width: 100%;
  resize: vertical;
  border: 0;
  background-color: #f2f2f2;
}

.button__control {
  border: none;
  background-color: #81b3d2;
  color: #ffffff;
  padding: 15px 50px;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.social__link {
  text-decoration: none;
}

.footer__copyright {
  text-decoration: none;
  color: #000000;
}

@media (min-width: 768px) {
  .footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}